html , body {
	padding: 0;
	margin: 0;
	line-height: 1.8em;
}

body, input, textarea {
	font-family: 'Fira Sans', sans-serif;
	color: #222;
	-webkit-font-smoothing: antialiased;
}

div.main-content {
	padding-top: 80px
}

h1 {
	font-size: 1.8em;
	margin: 0;
	padding: 0;
	line-height: 1.3em;
	font-weight: bold;
}

h2 {
	font-size: 1.4em;
	margin-bottom: .5em;
	font-weight: bold;
}

h4 {
	font-size: 1.2em;
	margin-bottom: .5em;
}

time {
	color: $light-grey;
}

header {
	text-align: center;
	padding-bottom: 40px;
}

img {
	max-width: 100%;
}

figure.full {
	width: 100vw;
	margin-left: 50%;
	transform: translateX(-50%);
}

ol li + li {
	margin: 20px 0 0 0;
}

footer {
	color: $light-grey;
	text-align: center;
	padding: 20px 0;
	font-size: .8em;
}

hr {
	 border: 0;
	 border-top: 1px solid #eee;
}

a {
	text-decoration: none;
	color: #007eff;
}

pre[class*="language-"] {
  border-radius: 6px;
}
