.post-body {
	padding-top: 1em;
}

.theme {
	display: flex;
	flex-wrap: wrap;

	.post {
		flex: 1 0 300px;
		height: 200px;
		display: inline-block;
		background-position: center;
		background-size: cover;
		margin: 5px 5px;

		a {
			width: 100%;
			height: 100%;
			display: block;
			background: rgba(0, 0, 0, .2);
			color: #fff;
			text-decoration: none;
			text-align: center;
			vertical-align: text-bottom;
			position: relative;
			transition:      all 0.2s ease;
			font-size: 1.1em;
			font-weight: 700;

			span {
				position: absolute;
				bottom: 5px;
				width: 100%;
				left: 0;
				text-shadow: 1px 1px 1px #000;
			}

			&:hover {
				background: none;
			}
		}
	}
}

a.ref {

	&:hover {
		color: black;
		text-decoration: none;
	}
}

.category {
	margin-top: 1em;
}
